*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  position: relative;
}
.header {
  color: #363636;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.125;
}
.is-1 {
  font-size: 3rem;
}
.is-2 {
  font-size: 2.5rem;
}
.is-3 {
  font-size: 2rem;
}
.is-4 {
  font-size: 1.5rem;
}
.is-5 {
  font-size: 1.25rem;
}
.is-6 {
  font-size: 1rem;
}
h1 {
  color: #363636;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.125;
  font-size: 3rem;
}
h2 {
  color: #363636;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.125;
  font-size: 2.5rem;
}
h3 {
  color: #363636;
  font-weight: 600;
  line-height: 1.125;
  font-size: 2rem;
}
h4 {
  color: #363636;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.125;
  font-size: 1.5rem;
}
h5 {
  color: #363636;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.125;
  font-size: 1.25rem;
}
h6 {
  color: #363636;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.125;
  font-size: 1rem;
}
.border {
  border-style: solid;
  border-width: 0.125rem;
  border-radius: 0.3125rem;
  border-color: lightgrey;
}
.page-container {
  min-width: min(720px, calc(100vw - 20px));
  max-width: 1440px;
  width: 70%;
  margin: auto;
}
